.dropdown-filter .data-specific {
    display: none;
}

.dropdown-filter .filter--box {
    position: relative;
}

.dropdown-filter .filter--box:after {
    pointer-events: none;
    position: absolute;
    content: "";
    top: 22px;
    right: 3%;
    margin: -5px 0 0 -5px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 7px 4px 0 4px;
    border-color: #3a343a transparent transparent transparent;
}

.dropdown-filter .data-group {
    margin: 20px 0 40px;
}

.dropdown-filter .data-group #Selectacountry {
    display: none;
}

.dropdown-filter .data-group .data-specific {
    -webkit-box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
            box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    padding: 20px 20px 15px;
}
